//申し込みボタン
.menu-entry-button {
 background-color: #fff;
 border-radius: 4px;
 color: #555;
 display: inline;
 font-weight: bold;
 line-height: 24px;
 margin: calc((60px - 40px) / 2);
 padding: 8px 36px;
 position: absolute;
 right: 10vw;
 text-decoration: none;
}


//フッターのシェアボタン
.share {
 a {
 float: left;
 line-height: 60px;
 height: 60px;
 width: 50%;
 text-decoration-line: none;
 &:hover {
 opacity: .8;
 }
 }
 &::after {
 content: '';
 clear: both;
 display: block;
 }
 .share-twitter,
 .share-facebook {
 display: block;
 }
}
.share-facebook {
 background-color: $facebook-color;
}
.share-twitter {
 background-color: $twitter-color;
}
.share-line {
 background-color: $line-color;
 display: none;
}
@media screen and (max-width: 800px) {
 .share a {
 width: 100%;
 }
 .share-line {
 display: block;
 }
}

.bg-main{
 background-color: $main-color;
}
.bg-ly {
 background-color: $light-yellow;
}

.title-text{
 font-family: 'Noto Sans JP', sans-serif;
 font-weight: bold;
}
 
/* 改行位置の調整 */
.ignore-pc {
 display: none;
}
.ignore-sp {
 display: inline;
}
@media screen and (max-width: 640px) {
 .ignore-pc {
 display: inline;
 }
 .ignore-sp {
 display: none;
 }
} 

// テーブル
table {
 border-collapse: collapse;
 border-spacing: 0;
}

table th,
table td {
 padding: .5em 1.5em;
 border: 1px solid #505050;
}

table thead th {
 background-color: #e8e8e8;
}

.no-padding {
 padding: 0;
}
