 .socials-list {
 a {
 font-size: 6em;
 }
 }

 .section-colored {
 color: #fff;
 background-color: $main-color;
 }

 .section-white {
 background-color: #fff;
 }

 .section-gray {
 background-color: $light-gray;
 }
}


.title-sponsor {
 position: relative;
 padding: 1.5rem;
 text-align: center;
 margin-top: 10px;
 margin-bottom: 10px;
}

.title-sponsor:before {
 position: absolute;
 bottom: -10px;
 left: calc(50% - 30px);
 width: 60px;
 height: 5px;
 content: '';
 border-radius: 3px;
 background: #000;
}

.top-contents-logo{
 //width: 10vw;
 width: 60%;
 margin-left: 20%;
 margin-right: 20%;
}

.about-title{
 font-size: 18px;
 font-weight: bold;
 padding: 1rem 2rem;
 border-top: 3px solid $main-color;
 background: #f4f4f4;
}


.list-style-none{
 list-style: none;
}
